import React from 'react'
import developer from './Assets/developer.svg'
import uidesign from './Assets/pen-tool.png'

export const Services = () => {
  return (
    <div id='Service' className=' mt-[150px] mb-[150px] w-full h-full flex flex-col justify-center items-center text-white font-body space-y-3'>
            <span className='font-bold text-[40px] pb-[50px]'>Services</span>
              <div className='md:flex md:justify-center md:items-center md:gap-16 h-full w-full'>
                  <div className='bg-slate-800 md:w-[500px] h-full flex flex-col justify-center items-center pt-[44px] pb-[36px] md:rounded-lg space-y-4 '>
                      <img src={uidesign} alt='no img' className='w-[55px] h-[50px] '/>
                      <h1 className='font-medium text-[25px] pb-[10px]'>UI/UX Design</h1>
                      <p className='w-full px-6 text-center text-[18px] pb-[20px]'>I value simple content structure, clean design patterns, and thoughtful interactions.</p>
                      <h1 className=' text-blue-600 text-[18px] font-light'>Things I enjoy designing :</h1>
                      <span className='text-white text-[20px] pb-[30px] '>UX, UI, Web, Apps, Logos</span>
                      <h1 className='text-blue-600 text-[18px] font-light'>Uisng Desing Tools :</h1>
                      <div className='flex flex-col justify-center items-center'>
                        <span className='text-white text-[18px] pb-[10px]'>Behance</span>
                        <span className='text-white text-[18px] pb-[10px]'>Figma</span>
                        <span className='text-white text-[18px] pb-[10px]'>Webflow</span>
                        <span className='text-white text-[18px] pb-[10px]'>Dribbble</span>
                        <span className='text-white text-[18px] pb-[10px]'>FontAwesome</span>
                      </div>                   
                  </div>
                  <div className='bg-slate-800 md:w-[500px] h-full flex flex-col justify-center items-center  pt-[40px] pb-[40px] md:rounded-lg space-y-4'>
                      <img src={developer} alt='no img' className='w-[50px] h-[50px] '/>
                      <h1 className='font-medium text-[25px] pb-[10px]'>Web Devlopment</h1>
                      <p className='w-full px-6 text-center text-[18px] pb-[20px]'>I like to code things from scratch, and enjoy bringing ideas to life in the browser.</p>
                      <h1 className='text-blue-600 text-[18px] font-light'>Languages I speak :</h1>
                      <span className='text-white text-[20px] pb-[30px]'>HTML, CSS, Js, React Js, Mysql, Php</span>
                      <h1 className='text-blue-600 text-[18px] font-light'>Using Dev Tools :</h1>
                      <div className='flex flex-col justify-center items-center'>
                        <span className='text-white text-[18px] pb-[10px]'>StackOverflow</span>
                        <span className='text-white text-[18px] pb-[10px]'>Github</span>
                        <span className='text-white text-[18px] pb-[10px]'>React Docs</span>
                        <span className='text-white text-[18px] pb-[10px]'>Tailwind css Docs</span>
                        <span className='text-white text-[18px] pb-[10px]'>Google</span>  
                      </div>
              </div>
            </div>
    </div>
  )
}
