import React from 'react'
import bgimg from './Assets/bgimage.svg'
import award1 from './Assets/award1.JPG'
import award2 from './Assets/award2.JPG'

export const BestMoment = () => {
  return (
    <div className='w-[100%] h-auto' style={{ backgroundImage: `url(${bgimg})` }} >
        <span className='pt-[20px] font-medium md:text-[26px] text-white text-[20px] flex flex-col justify-center items-center'>Best Moment</span>
        <div className=' p-[50px] md:flex md:space-x-10 justify-center items-center'>
          <img src={award1} alt='no img' className='md:h-[400px] w-auto pb-[30px]'/>
          <img src={award2} alt='no img' className='md:h-[400px] w-auto pb-[30px]'/>
        </div>
    </div>
  )
}
