import React, {useState } from "react";
import logo from "./Assets/logo.png";
import Resume from './Resume.pdf'

export const Navbar = () => {
  const Navitem = [
    {
      name: "Home",
      Eid: "intro",
    },
    {
      name: "About Me",
      Eid: "About Me",
    },
    {
      name: "Portfolio",
      Eid: "Portfolio",
    },
    {
      name: "Service",
      Eid: "Service",
    },
    {
      name: "Contact Us",
      Eid: "ContactUs",
    },
  ];

  const scrollToSection = (sectionId) => {
    const section = document.getElementById(sectionId);
    section.scrollIntoView({ behavior: "smooth" });
  };

  const handleDownload = () => {
    window.open(Resume);
  };

  let [open, setOpen] = useState(false);
  return (
    <div className=" md:flex pt-[22px] md:justify-between md:px-20 px-10 pb-8">
      <nav>
        <img
          src={logo}
          alt="no img"
          className="md:w-[200px] md:h-[50px] w-[150px] h-[30px] "
        />
      </nav>
      <div className="md:flex ">
        <div>
          <div
            onClick={() => setOpen(!open)}
            className="text-3xl absolute right-8 top-6 text-[#F10000] cursor-pointer md:hidden"
          >
            <ion-icon name={open ? "close" : "menu"}></ion-icon>
          </div>
          <ul
            className={`md:flex md:space-x-12 font-body text-white text-[16px] absolute bg-[#0F0328] md:pr-20 md:bg-none md:z-auto right-0  w-full md:w-auto md:pl-0 pl-9 transition-all ease-in pb-[20px] ${
              open ? "opacity-100" : "opacity-0 md:opacity-100"
            }`}
          >
            {Navitem.map((item) => (
              <li className="my-5 ">
                <div
                  onClick={() => scrollToSection(item.Eid)}
                  className="hover:text-[#FF5F2D]"
                >
                  {item.name}
                </div>
              </li>
            ))}
            <div className="pt-[10px] md:pl-[25px]">
              <button onClick={handleDownload} className="bg-[#F10000] rounded-md p-3 text-white font-normal ">
                Download CV
              </button>
            </div>
          </ul>
        </div>
      </div>
    </div>
  );
};
