import React from "react";
import img from "./Assets/my image.jpg";
import dribbble from "./Assets/dribbble-icon.svg";
import behance from "./Assets/devicon_behance.svg";
import github from "./Assets/github-icon.svg";
import instagram from "./Assets/instagram.svg";
import mail from "./Assets/gmail.svg";
import linkedin from "./Assets/linkedin.svg";

export default function About() {
  return (
    <div id="About Me" >
      <div className="md:ml-32 md:mr-32 bg-slate-800 mt-20 pb-20 md:flex">
        <img
          src={img}
          className="md:w-[500px] md:h-[600px] pt-20 px-10 md:pl-20"
          alt="no img"
        />
        <div className="px-[26px] w-full ">
          <p className="text-white font-medium md:text-xl text-[16px] pt-20 font-body">
            Hear about me!
          </p>
          <p className="text-white font-medium text-base pt-5 md:pr-16 font-body">
            Hi, Im Abdul Sirajudeen. I'm a multi-talented human with over 1
            years of experiences in wide range of design disciplines.
          </p>
          <p className="text-white font-medium text-base pt-5 md:pr-16 font-body">
            You can also call me a product designer, experience designer,
            interaction, UI, UX or by any other market defined function-title.
            I'm also a multi-disciplinary maker with over 2 years of experiences
            in wide range of design disciplines, front-end developer and UI/UX
            designer.
          </p>
          <p className="text-white font-medium pt-5 md:pr-16  text-lg font-body">
            I am a talented, hardworker UI/UX design and front end developer
            from Tamil Nadu
          </p>
          <p className="text-white font-medium text-base pt-5 md:pr-16 font-body">
            I have completed a BCA (Computer Application) and am currently
            pursuing a Master of Computer Application degree. I have a strong
            knowledge of UI/UX design, React, and Python. Additionally, I have
            successfully developed one personal projects using React js,
            Tailwind css, js and five personal project using Figma, Adobe Xd,
            Photoshop, Illustrator.
          </p>
          <p className="text-white font-medium text-base pt-5  md:pr-16 font-body">
            Stay connected with us !
          </p>
          <div className="text-white font-medium text-base pt-5  md:pr-16 font-body grid grid-cols-2 md:grid-cols-3 gap-6">
            <a href="https://dribbble.com/abdsiraj" className="flex">
              <img
                src={dribbble}
                alt="no img"
                className="w-[30px] h-[30px] mr-[20px]"
              />
              <span>Dribbble</span>
            </a>
            <a href="https://www.behance.net/abdsiraj" className="flex">
              <img
                src={behance}
                alt="no img"
                className="w-[30px] h-[30px] mr-[20px]"
              />
              <span>Behance</span>
            </a>
            <a href="https://github.com/Sirajudeen2001" className="flex">
              <img
                src={github}
                alt="no img"
                className="w-[30px] h-[30px] mr-[20px] "
              />
              <span>github</span>
            </a>
            <a
              href="https://www.instagram.com/as_creation_ui_designer/"
              className="flex"
            >
              <img
                src={instagram}
                alt="no img"
                className="w-[30px] h-[30px] mr-[20px]"
              />
              <span>Instagram</span>
            </a>
            <a
              href="mailto:sirajcsc2000@gmail.com"
              className="flex"
            >
              <img
                src={mail}
                alt="no img"
                className="w-[30px] h-[30px] mr-[20px]"
              />
              <span>Gmail</span>
            </a>
            <a
              href="https://www.linkedin.com/in/abdul-sirajudeen-s-9a3393225/"
              className="flex"
            >
              <img
                src={linkedin}
                alt="no img"
                className="w-[30px] h-[30px] mr-[20px]"
              />
              <span>Linkedin</span>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}
